import Vue from 'vue'
import Router from 'vue-router'
import HelloContainer from "./components/HelloWorld.vue"
import home from "./components/home.vue"
import head from "./components/fixed/clsHead.vue"
import indexBanner from "./components/indexBanner/indexBanner.vue"
import starDetails from "./components/details/starDetails.vue"
import shopping from "./components/details/shopping.vue"
import addShopCart from "./components/details/addShopCart.vue"
import login from "./components/login/login.vue"
import register from "./components/register/reg.vue"
import pay from "./components/pay/pay.vue"
import toPay from "./components/pay/toPay.vue"

Vue.component("my-starDetails",starDetails);
Vue.component("my-indexBanner",indexBanner);
Vue.use(Router)

export default new Router({
  routes: [
    {path:'/',component:home,children:[ 
      {path:'/',component:head}, 
      {path:"/shop",component:shopping}, 
      {path:"/shop/:id",component:shopping}, 
      {path:"/shopCart/:id",component:addShopCart}
    ]},
    {path:"/login",component:login}, 
    {path:"/reg",component:register}, 
    {path:"/pay",component:pay}, 
    {path:"/toPay",component:toPay}, 
    // {path:'/home/head',component:head},
  ]
})
