<template>
  <div>
    <div class="pay">
      <div class="container">
      <!--<div></div>-->
        <div class="payHead1">
          <img src="img/reg--login/xmFirst.png" @click="jumpFirst">
          <span>我的购物车</span>
          <span v-if="num>0">温馨提示：产品是否购买成功，以最终下单为准哦，请尽快结算</span>
        </div>
        <div class="payHead2">
          <div class="show" @mouseleave="hide">
            <span @mouseenter="show" >{{user}}
              <span>&lt;</span></span>
            <ul v-if="showFlag==1">
              <li>个人中心</li>
              <li>评价晒单</li>
              <li>我的喜欢</li>
              <li>小米账号</li>
              <li >退出登录</li>
            </ul>
          </div>
          |<span>我的订单</span>
          
        </div>
      </div>

    </div>

    <div class="payContent">
      <div class="container" v-if="num>0">
        <div class="contentHead">
          <ul>
            <li><span @click="AllSelClick" @mouseenter="showAllSelEnter" @mouseleave="showAllSelLeave"  >✔</span>全选</li>
            <li>商品名称</li>
            <li>单价</li>
            <li>数量</li>
            <li>小计</li>
            <li>操作</li>
          </ul>
          
        </div>
        <div class="contentBody">
        
          <ul v-for="(item,i) of data" :key="i">
            <li><span @mouseenter="showSelEnter(i)" @mouseleave="showSelLeave(i)" @click="showSelClick(i)">✔</span> <img :src="item.pic"> </li>
            <li>{{item.xmname}} {{item.vs}} {{item.color}}</li>
            <li>{{item.price}} 元</li>
            <!--{{item.num}}-->
            <li>
              <span @click="reduceNum(i)">-</span> 
              <input v-model="item.num" @blur="changeHand(i)">
              <span @click="addNum(i)">+</span> 
            </li>
            <li>{{item.subtotal}} 元</li>
            <li @click="delDetailClick(i)"><span>X</span></li>
          </ul>
        </div>
      </div>
      <div class="isWithout" v-else>
        <img src="img/without_cart.png" >
        <button @click="shopingClick">马上去购物</button>
      </div>
      <div v-if="num>0" class="toPayFix">
        <div class="toPay"  >
          <div>
            <span>继续购物</span>|
            <span>共 
              <span>{{num}}</span> 件商品，已选择 <span>{{mySel}}</span> 件
            </span>
          </div>
          <div>
            <span>合计：<span>{{payTotal}} </span>元 </span>
            
            <button @click="PayClickOk">去结算</button>
          </div>
        </div>
      </div>

    </div>

  </div>
</template>
<script>
export default {
  data() {
    return {
      showFlag:0,
      user:"",
      data:[],
      Sel:[],
      Add_Reduce:0,
      selAll:0,
      num:0,
      SelCnt:0,
      savenum:0,
      mySel:0,
      payTotal:0,
      // 判断支付按钮是否可点击的标志位
      judgePay:0,
      // 
      wdHeight:0,
      wdWidth:0,
      scrollFlag:0,
      fixFlag:0
    }
  },
  methods: {
    load(){
      
      var parent=this;
      if(sessionStorage.getItem("reloadFlag")==1){
        sessionStorage.setItem("reloadFlag",0);
        window.location.reload();
      }
        window.onmousemove=function(e){
          // console.log(e.offsetY);
          // console.log(e)
          // client
          // console.log(222)
        }
          // console.log("inHeight："+window.innerHeight)
        
        if(window.innerWidth>1226){
          this.wdWidth = (window.innerWidth-1226)/2;
        }
        // 当窗口发生改变时 onresize 触发事件
        window.onresize = function(){ 
          parent.fixPayBtn();
        }
        window.onscroll=function(e){
          // document.documentElement.scrollTop
          parent.fixPayBtn();
          // console.log(window.innerHeight)
          
        }
    
        this.user=sessionStorage.getItem("uname");
        this.initFun();
      /********************************************************************* */
 
      /************************************************************** */

        // this.mySelFun();
    },
    PayClickOk(){
      if(this.judgePay==1){//选择了商品，可点击去支付
        //  
        this.$router.push("/toPay");
      }
    },
    // fixed Pay button list 固定支付按钮行
    fixPayBtn(){
      if(window.innerWidth>1226){
        this.wdWidth = (window.innerWidth-1226)/2;
      }
      var toPayBtn = document.querySelector('.toPayFix');
      var contentList = document.querySelector('.contentBody>ul:last-child');
      if(toPayBtn!=null && contentList!=null){
      var listTop=contentList.getBoundingClientRect().top;
        var listHeight=contentList.offsetHeight
        // 获取 元素在 相对视口 位置
        // var top = toPayBtn.getBoundingClientRect().top;
          
          if(listTop+listHeight+100 > window.innerHeight){
            toPayBtn.style.position="fixed";
            toPayBtn.style.bottom = -20 +"px";
            toPayBtn.style.left = this.wdWidth - 10  + "px";
          }else{
            toPayBtn.style.position="relative";
            toPayBtn.style.bottom="";
            toPayBtn.style.left = "";
          }

      }

    },
    // 
    toPayCnt(){
      var btnPay = document.querySelector(".toPay button");
      console.log(btnPay)
      if(this.mySel>0){//判断已选商品的数量
        btnPay.style.background="#ff6700";
        btnPay.style.color="#fff";
        this.judgePay = 1;
      }else{
        btnPay.style.background="#E0E0E0";
        btnPay.style.color="#B0B0B0";
        this.judgePay = 0;
      }
    },
    // 初始化
    initFun(){
    
      var url="http://binyuailly.github.io/xm/shoped?"
      url+="uname="+this.user;
      this.SelCnt=0;
      this.mySel=0;
      this.payTotal=0;
      this.axios.get(url).then(result=>{
        this.data=result.data
        // console.log(this.data,result.data)
        console.log(result.data)
        for(var i=0;i<result.data.length;i++){
          
          var subtotal=result.data[i].price * result.data[i].num;
          this.data[i].subtotal=subtotal;
          // this.data[i].closeFlag=1;
          this.num+=this.data[i].num;
          this.Sel[i]=this.data[i].flag;
          this.SelCnt+=this.Sel[i];
          if(this.Sel[i]==1){
            this.mySel+=Number(this.data[i].num);
            this.payTotal+=this.data[i].price * this.data[i].num;
          }
          
        }
        
        
        
        if(this.SelCnt<result.data.length){
          // sessionStorage.setItem("selAll",0)
          this.selAll=0;
        }
        else if(this.SelCnt==result.data.length){
          // sessionStorage.setItem("selAll",1)
          this.selAll=1;
        }
        sessionStorage.setItem("addCart",this.num);
        
      })     
      setTimeout(()=>{//使页面加载完全后在执行
      // 判断支付按钮是否可点
        this.toPayCnt();

        var bgAllSel = document.querySelector(`.contentHead>ul>li:nth-child(1)>span`);
        if(bgAllSel!=null){
          if(this.selAll==1){
            bgAllSel.style.background="#ff6700";
          }else{
            bgAllSel.style.background="#fff";
          }
        }
        var bgSel = document.querySelectorAll(`.contentBody>ul>li:nth-child(1)>span`);
        for(var i=0;i<this.Sel.length;i++){
          if(this.Sel[i]!=1){
            bgSel[i].style.background="#fff";
          }else{
            bgSel[i].style.background="#ff6700";
          }
        }

      },10)
    },
    // 选取 要支付的商品
    mySelFun(){
      this.mySel=0;
      this.num=0;
      this.payTotal=0;
      for(var i=0;i<this.Sel.length;i++){
        if(this.Sel[i]==1){
          this.mySel+=Number(this.data[i].num);
          this.payTotal+=this.data[i].price * this.data[i].num;
        }
        this.num+=Number(this.data[i].num);
      }
      this.toPayCnt();
      // if(this.Sel.length<=1){
        // if(this.mySel>this.num){
          // this.num=this.mySel;
        // }        
      // }else{
        // if(this.mySel>this.num){
        //   this.num=this.mySel;
        // }  
      // }

    },
    // 商品数据修改
    upNumFun(num,i){
      
        if(this.savenum!=num){
          this.savenum=num;
        }else{
          return;
        }

        var url="http://binyuailly.github.io/xm/upNum"
        url+="?num="+num;
        url+= "&color="+this.data[i].color;
        url+= "&uname="+sessionStorage.getItem("uname");
        url+= "&xmname="+this.data[i].xmname;
        url+= "&vs="+this.data[i].vs;
        this.mySelFun();
        this.axios.get(url).then(result=>{
          console.log(result);
        })

    },
    jumpFirst(){
      sessionStorage.setItem("reloadFlag",1);
      this.$router.push("/")
    },
    changeHand(i){//手动修改 商品数量
      if(this.data[i].num>10){
        this.data[i].num=10;
      }
      if(this.data[i].num<1){
        this.data[i].num=1;
      }
      console.log(1,this.data[i].num)

      var subtotal=this.data[i].price * this.data[i].num;
      // console.log(subtotal)
      // this.data[i].subtotal=subtotal;
      // 主动刷新页面subtotal数据
      var obj = this.data[i];
      obj.subtotal = subtotal;
      // this.data[i] = obj;
      
      this.$set(this.data,i,obj)

      // console.log(this.data)
      // console.log(1,this.data[i].subtotal)
      var addnum=this.data[i].num
      // 跟新数据库num
      this.upNumFun(addnum,i);
      // window.location.reload();
    },
    //
    addNum(i){
      var i=i;
      if(this.Add_Reduce==0){
        this.Add_Reduce=1;
        if(this.data[i].num<10){
          this.data[i].num++;
        }
        this.data[i].subtotal=this.data[i].price * this.data[i].num;
        setTimeout(()=>{
          this.Add_Reduce=0;
        },500)

        var addnum=this.data[i].num
        this.upNumFun(addnum,i);
      }
      // console.log(1)
    },
    // 
    reduceNum(i){
      var i=i;
      if(this.Add_Reduce==0){
          this.Add_Reduce=1;
        if(this.data[i].num>1){
          this.data[i].num--;
        }
        this.data[i].subtotal=this.data[i].price * this.data[i].num
        setTimeout(()=>{
          this.Add_Reduce=0;
        },500)
        var addnum=this.data[i].num
        this.upNumFun(addnum,i);
      }

      // console.log(0)
    },
    // 
    shopingClick(){
      sessionStorage.setItem("reloadFlag",1);
      // 出现此 按钮时 购物车已没有商品了
      sessionStorage.setItem("addCart",0);
      this.$router.push("/")
    },
    delDetailClick(i){
      // var ulDetail = document.querySelectorAll(".contentBody>ul");
      // console.log(ulDetail)
      // console.log("color"+this.data[i].color)
      // this.data[i].closeFlag=0;
      // var obj = this.data[i];
      // obj.closeFlag=0;
      // this.$set(this.data,i,obj);
      // 修改商品数量的显示
      this.num-=this.data[i].num;
      this.mySel-=this.data[i].num;

      var url="http://binyuailly.github.io/xm/payDel?uname="+sessionStorage.getItem("uname");
      url+="&xmname="+this.data[i].xmname;
      url+="&vs="+this.data[i].vs;
      
      url+="&color="+this.data[i].color;
      // console.log(url)
      this.axios.get(url).then(result=>{
        console.log("del:",result);
        if(result.data.code==1){

        }
      })
      // 等于总价 减已删除的商品小计
      if(this.Sel[i]==1){
        this.payTotal-=this.data[i].subtotal;
        
      }else{
        // this.Sel[i]=1;
      }
      
      console.log(this.data);
      // 剪切掉已删除的数据
      this.data.splice(i,1);
      this.Sel.splice(i,1);
      /*************************************** */
      var bgSel = document.querySelectorAll(`.contentBody>ul>li:nth-child(1)>span`);
      var bgAllSel = document.querySelector(`.contentHead>ul>li:nth-child(1)>span`);
      this.SelCnt=0;
      this.mySel=0;
      for(var j=0;j<this.Sel.length;j++){
        this.SelCnt += this.Sel[j];
        if(this.Sel[j]==1){
          // 更新页面 已选商品 数据
          this.mySel+=Number(this.data[j].num);
          bgSel[j].style.background="#ff6700";
        }else{
          bgSel[j].style.background="#fff";
        }
        if(this.SelCnt==this.Sel.length){
          bgAllSel.style.background="#ff6700";
        }else{
          bgAllSel.style.background="#fff";
        }

      }
      // 支付按钮的修饰判断
      this.toPayCnt();
      // var obj=this;
      // obj.mySel=this.mySel;
      // this.$set(this,"mySel",obj);
      /******************************************** */

      console.log("this.Sel",this.Sel)
      // sessionStorage.setItem("Sel",this.Sel.join(","));
      // var delUl=document.querySelector(`.contentBody>ul:nth-child(${i+1})`);
      // if(delUl!=null)  delUl.remove();
      // document.querySelector(`.contentBody>ul:nth-child(${i+1})`).style.display="none";

      // var bgSel = document.querySelectorAll(`.contentBody>ul>li:nth-child(1)>span`);
      // console.log(bgSel);

      // this.SelCnt=0;
      // var bgSel = document.querySelectorAll(`.contentBody>ul>li:nth-child(1)>span`);
      // for(var j=0;j<bgSel.length;j++){
      //   this.SelCnt += this.Sel[j];
      // }
      // if(this.SelCnt==bgSel.length){
      //   this.selAll = 1;
      //   bgAllSel.style.background="#ff6700";
      // }else{
      //   this.selAll = 0;
      //   bgAllSel.style.background="#fff";
      // }
      // window.location.reload();
    },
    // 全选按钮
    AllSelClick(){
      var bgSel = document.querySelectorAll(`.contentBody>ul>li:nth-child(1)>span`);
      var bgAllSel = document.querySelector(`.contentHead>ul>li:nth-child(1)>span`);
      this.selAll=1-Number(this.selAll);
      if(this.selAll==1){
        bgAllSel.style.background="#ff6700";
        bgAllSel.style.color="#fff";
        for(var i=0;i<bgSel.length;i++){
          bgSel[i].style.background="#ff6700"
          this.Sel[i]=1;
        }
      }else{
        bgAllSel.style.background="#fff";
        bgAllSel.style.color="#666";
        for(var i=0;i<bgSel.length;i++){
          bgSel[i].style.background="#fff";
          this.Sel[i]=0;
        }

        // 
      }
      sessionStorage.setItem("selAll",this.selAll)
      
      this.mySelFun();
      // sessionStorage.setItem("Sel",this.Sel.join(","))
      var url="http://binyuailly.github.io/xm/flagUpAll?flag="+this.selAll;
      
      url+= "&uname="+sessionStorage.getItem("uname");
      this.axios.get(url).then(result=>{
        console.log(result)
      })
      
      // console.log(sessionStorage.getItem("Sel").split(","))
      
    },
    showAllSelEnter(){
      var bgAllSel = document.querySelector(`.contentHead>ul>li:nth-child(1)>span`);
      if(this.selAll==1){
        bgAllSel.style.color="#fff";
      }else{
        bgAllSel.style.color="#666";
      }

    },
    showAllSelLeave(){
      var bgAllSel = document.querySelector(`.contentHead>ul>li:nth-child(1)>span`);
      bgAllSel.style.color="#fff";

    },
    showSelEnter(i){
      var bgSel = document.querySelectorAll(`.contentBody>ul>li:nth-child(1)>span`);
      if(this.Sel[i]==1){
        bgSel[i].style.color="#fff";
      }else{
        bgSel[i].style.color="#666";
      }
      console.log(i,1)
    },
    showSelLeave(i){
      var bgSel = document.querySelectorAll(`.contentBody>ul>li:nth-child(1)>span`);
      
        bgSel[i].style.color="#fff";
     

      console.log(i,0)
    },
    showSelClick(i){
      
      // this.Sel = sessionStorage.getItem("Sel").split(",")
      this.Sel[i]=1 - Number(this.Sel[i]);
      console.log("SHOWSEL:"+this.Sel[i])
      var bgSel = document.querySelectorAll(`.contentBody>ul>li:nth-child(1)>span`);
      if(this.Sel[i]==1){
        bgSel[i].style.background="#ff6700";
        bgSel[i].style.color="#fff";
      }else{
        bgSel[i].style.background="#fff";
        bgSel[i].style.color="#666";
      }
      var sum=0;
      for(var j=0;j<this.Sel.length;j++){
        sum += Number(this.Sel[j]);
      }
      console.log("sum:"+sum)
      sessionStorage.setItem("SelSum",sum)
      var bgAllSel = document.querySelector(`.contentHead>ul>li:nth-child(1)>span`);
      if(sum<this.Sel.length){
        this.selAll=0;
        bgAllSel.style.background="#fff";
      }else if(sum==this.Sel.length){
        this.selAll=1;
        bgAllSel.style.background="#ff6700";
      }
      sessionStorage.setItem("selAll",this.selAll)
      this.mySelFun();
      var url="http://binyuailly.github.io/xm/flagUp?flag="+this.Sel[i];
      url+= "&color="+this.data[i].color;
      url+= "&uname="+sessionStorage.getItem("uname");
      url+= "&xmname="+this.data[i].xmname;
      url+= "&vs="+this.data[i].vs;
      this.axios.get(url).then(result=>{
        console.log(result)
      })
      // sessionStorage.setItem("Sel",this.Sel.join(","))
      // console.log(bgSel)
    },
    show(){
      this.showFlag=1;
    },
    hide(){
      this.showFlag=0;
    }
  },
  created() {
    // this.load();
  },  
  mounted() {
    this.load();
  },
  
}
</script>
<style scoped>
/************* */
  .pay{
    background:#fff;
    border-bottom:2px solid #ff6700;
  }
  .payHead2>span:hover,
  .show>span:hover,
  .show>ul>li:hover,
  .contentBody>ul>li:nth-child(1)>span:hover,
  .contentHead>ul>li:nth-child(1)>span:hover{
    cursor:pointer;
  }
  .show>ul>li:hover{
    background:#ddd;
    color:#ff6700;
  }
  .show>span:nth-child(1):hover{
    color:#ff6700;
  }
  .pay>.container{
    position:relative;
    height:102px;
    display:flex;
    justify-content:space-between;
    // align-items:center;
  }
  .payHead2,.show{
    position:relative;
  }
  .show{
    top:20px;
    z-index:10;
  }
  .payHead2{
    display:flex;
  }
  .show>ul{
    position:absolute;
    text-align:center;
    background:#fff;
    border:1px solid #ddd;
    padding-bottom: 10px;
    border-top: transparent;
    left:10px;
  }
