<template>
  <div class="cart">
    <div class="container">
      <div class="addShopCart">
        <img src="img/addShopCart/success.jpg">
        <!--<div></div>-->
        <div>
          <span>已成功加入购物车!</span>
          <p>{{goods.xmname}} {{goods.vs}} {{goods.color}}</p>
        </div>
        <ul>
          <li @click="prevClick">返回上一级</li>
          <li  @click="payClick">去购物车结算</li>
        </ul>
        
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      lid:0,
      goods:{}
    }
  },
  created() {
    this.show();
  },
  methods: {
    show(){
      // 重加载一次
      if(sessionStorage.getItem("reloadFlag")==1){
        sessionStorage.setItem("reloadFlag",0);
        window.location.reload();
      }

      // console.log(sessionStorage.getItem("goodsInfo"))
      var goods = sessionStorage.getItem("goodsInfo");
      this.goods=JSON.parse(goods);
      console.log(this.goods)
      this.lid=this.$route.params.id;
    },
    // 去购物车
    payClick(){
      sessionStorage.setItem("reloadFlag",1);
      this.$router.push("/pay")
    },
    // 上一页
    prevClick(){
      sessionStorage.setItem("reloadFlag",1);
      this.$router.push(`/shop/${this.lid}`);
    }
  },
  mounted() {
    
  },
}
</script>
<style scoped>
  .container,.cart{
    background:#eee;
  }
  .addShopCart{
    height:120px;
    position:relative;
  }
  .addShopCart>ul{
    position:absolute;
  }
  .addShopCart,.addShopCart>ul{
    display:flex;
  }
  .addShopCart>ul{
    right:0px;
    top:40px;
  }
  .addShopCart>ul>li{
    font-size:14px;
    width:180px;
    padding:7px 0;
    margin:0 10px;
    text-align:center;
    // border:1px solid #0AF;
    transition:1s;
  }
  .addShopCart>ul>li:nth-child(1){
    border:1px solid #999;
    color:#888;
  }
  .addShopCart>ul>li:nth-child(1):hover{
    background:#fff;
  }
  .addShopCart>ul>li{
    cursor:pointer;
  }
  .addShopCart>ul>li:nth-child(2){
    background:#ff6700;
    color:#fff;
  }
  .addShopCart>ul>li:nth-child(2):hover{
    background:#f25807;
  }
  .addShopCart>img{
    width:64px;height:64px;
    margin-top:28px;
  }
  .addShopCart>div>span{
    font-size:24px;
  }
  .addShopCart>div>p{
    margin:5px 0;
  }
  .addShopCart>div{
    margin-top:39px;
    margin-left:10px;
  }

</style>