<template>
  <div class="details">
    <div class="container">
      
      <div>
        <h2 class="titleDetails">明星产品</h2>
        <div class="star">
          <div class="star1" @click="enterShop(0)">
            <img :src="data[0].src">
            <div class="detailStart1">
              <h3><a>{{data[0].xmname}}</a></h3>
              <p class="JS">{{data[0].info}}</p>
              <p class="price">{{minPrice[0]}}<span>元起</span></p>
              <p class="LJ"><a>了解产品  &gt;</a></p>
            </div>
          </div>
          <div class="star2" v-for="(item,i) of data" :key="i" @click="enterShop(i)" v-if="i!=0">
            <img :src="item.src">
            <div>
              <h3><a>{{item.xmname}}</a></h3>
              <p class="JS">{{item.info}}</p>
              <p class="price">{{minPrice[i]}}<span>元起</span></p>
            </div>
          </div>
          <!--<div class="star2">
          
          </div>-->
        </div>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  data() {
    return {
      data:[],
      minPrice:[]
    }
  },
  created() {
    this.load();
  },
  methods: {
    load(){
      // 重加载一次
      if(sessionStorage.getItem("reloadFlag")==1){
        sessionStorage.setItem("reloadFlag",0);
        window.location.reload();
      }
      var url = "http://binyuailly.github.io/xm/details"
      this.axios.post(url).then((result)=>{
        // console.log(result.data);
        this.data=result.data;
        for(var i=0;i<this.data.length;i++){
          // 取最小价格
          this.minPrice[i]=
            Math.min.apply(/*apply将数组打散 */
              null,
              this.data[i].price.split(" ")
            );
        }
        // console.log(this.minPrice)
        var dataParse=JSON.stringify(result.data)
        sessionStorage.setItem("xmdata",dataParse);
        return;
      })
    },
    enterShop(i){
      // 跳转 传参
      sessionStorage.setItem("reloadFlag",1);
      this.$router.push(`/shop/${i}`);
      
    }

  },
  watch: {
    
  },
}
</script>
<style scoped>
  .star1,.star2{
    transition: 0.3s ;
    
  }
  .star1:hover,.star2:hover{
    transform:translate(0px,-5px);
    box-shadow:0px 0px 10px #ddd;

  }
  .details,.container{
    background:#f3f3f3;
  }
  .star{
    display:flex;
    flex-wrap:wrap;
    justify-content:space-between;
  }
  .star1,.star2{
    margin-bottom:15px;
    position:relative;
    background:#fff;
  }
  .star1{
    height:434px;
    width:1226px;
    
  }
  .star1>img{
    width:854px;
    height:100%;
  }
  .star2>img{
    width:606px;
    height:358px;
    
  }
  .star2{
    width:606px;
    height:482px;
  }
  .titleDetails{
    line-height:2;
  }
  .detailStart1{
    width:270px;
    height:340px;
    position:absolute;
    top:94px;right:40px;
    justify-content:center;
  }
  .detailStart1>h3>a:nth-child(1){
    color:#666;
    font-size:32px;
    font-weight:500;
  }
  .detailStart1>.JS{
    margin-top:10px;
    padding-bottom:10px;
    font-size:14px;
  }
  .detailStart1>.price{
    color:#FF6700;
    font-size:32px;
    margin-top:40px;
  }
  .detailStart1>.price>span{
    font-size:14px;
  }
  .detailStart1>.LJ>a:hover,.star2>div>h3>a:hover,.star1>img:hover,.star2>img:hover{
    cursor:pointer;
  }
  .detailStart1>.LJ>a{
    position:relative;
    top:100px;left:0px;
    color:#888;
  }
  .star2>div{
    position:relative;
    margin:38px 0 0 48px;
  }
  .star2>div>h3>a{
    font-weight:500;
    color:#555;
  }
  .star2>div>.price{
    position:absolute;
    top:20px;right:70px;
  }
  .star2>div>.price>span{
    font-size:14px;
  }
  .star2>div>.price{
    font-size:30px;
  }
</style>