<template>
  <div class="shopping">
    <div class="container">
      <div class="box_details">
        <div class="fix">
          <div class="show_pic" @mouseenter="enterDIV" @mouseleave="leaveDIV">

            <img :src="show_src" v-model="show_src" @mouseenter="enterImg" @mouseleave="leaveImg" >

            <span @click="leftClick" @mouseenter="enterSpan" @mouseleave="leaveSpan">&lt;</span>

            <span @click="rightClick" @mouseenter="enterSpan" @mouseleave="leaveSpan">&gt;</span>

            <ul @mouseenter="enterUl" @mouseleave="leaveUl">
              <li></li>
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </div>
        </div>
        <div class="show_details">
          <h1 class="show1"><span>{{data[lid].xmname}}</span></h1>
          <p class="show2">{{data[lid].details}}</p>
          <p class="show3">小米自营</p>
          <div class="show4">
            <span>赠品</span>
            <span>赠米粉卡，最高含100元话费</span>

          </div>
          <div class="show5">选择版本</div>
          <ul class="show6">
            <li v-for="(vsItem,vsi) of data[lid].vs" :key="vsi" @click="vsClick(vsi)">{{vsItem}}</li>
          </ul>
          <div class="show7">选择颜色</div>
          <ul class="show8" >
            <li v-for="(colorItem,colori) of data[lid].color" :key="colori" @click="colorClick(colori)">{{colorItem}}</li>
          </ul>
          <div class="show9">选择套餐</div>
          <ul class="show10">
            <li>标配</li>
            <li>聆听音乐套餐</li>
          </ul>
          <div class="show11">
            <ul>
              <li>{{goods.xmname}} {{goods.vs}} {{goods.color}}<span>{{goods.price}}元</span>   </li>
              <li>   总计   ：{{goods.price}}元  </li>
            </ul>
          </div>
          <ul class="show12">
            <li @click="outShop">加入购物车</li>
            <li>♥  喜欢</li>
          </ul>

        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      init1:null,
      img_index:0,
      show_src:"",
      lid:0,
      // moveCnt 记录 第几张轮播 图
      moveCnt:0,
      data:[],
      goods:{xmname:"",vs:"",color:"",price:"",src:""}
    }
  },
  created() {
    
  },
  methods: {
    show(){
      // 重加载一次
      if(sessionStorage.getItem("reloadFlag")==1){
        sessionStorage.setItem("reloadFlag",0);
        window.location.reload();
      }

      this.lid=this.$route.params.id;
      var lid=this.lid;
      // console.log(lid)
     var data=sessionStorage.getItem("xmdata");
     this.data=JSON.parse(data);
      // console.log(this.data);
      // 将 color price vs 转换为数组
     this.data[lid].color=this.data[lid].color.split(" ");
     this.data[lid].price=this.data[lid].price.split(" ");
     this.data[lid].vs=this.data[lid].vs.split(" ");
     this.data[lid].src1=this.data[lid].src1.split(" ");

      //  初始化goods
      this.goods.xmname=this.data[lid].xmname;
     this.goods.vs=this.data[lid].vs[0];
     this.goods.price=this.data[lid].price[0];
     this.goods.color=this.data[lid].color[0];
     this.goods.src=this.data[lid].src1;
     this.show_src=this.goods.src[0];
      //  console.log(this.goods.src)

     window.onscroll=function(){
       var box= document.querySelector(".show_pic");
     var pic_box= document.querySelector(".show_pic>img");
     var pic_move= document.querySelectorAll(".show_pic>span");
     var pic_ul= document.querySelector(".show_pic>ul");
      //  console.log(pic_box)
      //  console.log(document.documentElement.scrollTop);
       var top=document.documentElement.scrollTop;
       if(top<300){
         box.style.position="fixed";
         box.style.top="150px";
       }else{
         
         box.style.position="absolute";
         box.style.top="261px";
       }
     }
     
    },
    // 进入轮播条 就清除定时器
    enterUl(){
      this.shift_click();
      clearInterval(this.init1);
      this.init1=null;
    },
    leaveUl(){
      // this.shift_pic();
    },
    // 点击轮播条切换图片
    shift_click(){
      var pic_lis= document.querySelectorAll(".show_pic>ul>li");

      for(var li=0;li<pic_lis.length;li++){
        // 将下标保存在index 中
        pic_lis[li].index=li;
        var parent=this;
        pic_lis[li].onclick=function(){
          // console.log(this.index)
          parent.moveCnt=this.index;
          parent.show_src=parent.goods.src[parent.moveCnt]
          for(var i of pic_lis){
            i.style.background="#ccc";
          }
          
          this.style.background="#999";
        }
      }
    },
    // 自动轮播 修改 轮播条样式
    auto_move(){
      // 轮播条
      var pic_lis= document.querySelectorAll(".show_pic>ul>li");
      for(var li of pic_lis){
        li.style.background="#ccc";
      }
      pic_lis[this.moveCnt%4].style.background="#999";

      this.show_src=this.goods.src[this.moveCnt];
    },
    leftClick(){
      this.moveCnt--;
      if(this.moveCnt < 4 * this.img_index){
        this.moveCnt=4 * (this.img_index+1) - 1;
      }
      // 轮播条
      this.auto_move();

      this.show_src=this.goods.src[this.moveCnt];
    },
    rightClick(){
      this.moveCnt++;
      if(this.moveCnt > 4 * (this.img_index+1) - 1){
        this.moveCnt=4 * this.img_index;
      }
      
      this.show_src=this.goods.src[this.moveCnt];
      // 轮播条
      this.auto_move();

    },
    enterDIV(){
      // console.log(333)
      clearInterval(this.init1);
      this.init1=null;
    },
    leaveDIV(){
      // console.log(344)
      this.shift_pic();
    },
    enterSpan(){
      clearInterval(this.init1);
      this.init1=null;
    },
    leaveSpan(){
      this.shift_pic();
    },
    // img  --> mouseenter
    enterImg(){
      // console.log(111)
      // clearInterval(this.init1);
      // this.init1=null;
    },
    // img  --> mouseleave
    leaveImg(){
      // console.log(222)
      // this.shift_pic();
    },
    // 加入购物车按钮
    outShop(){
      // 加入购物车按钮
      sessionStorage.setItem("reloadFlag",1);

      if(sessionStorage.getItem("loginFlag")==1){//跳转到购物车页面
        sessionStorage.setItem("goodsInfo",JSON.stringify(this.goods));
        // sessionStorage.setItem("addCart",1)
        var addCart=sessionStorage.getItem("addCart");
        // addCart=Number(addCart);
        console.log(addCart)
        if(addCart==null){
          addCart=1;
        }else{
          addCart++;
        }
        // console.log("addCart"+addCart)
        sessionStorage.setItem("addCart",addCart);
        var url="http://binyuailly.github.io/xm/addcart?";
        url+="pic="+this.goods.src[parseInt(this.moveCnt/4)*4]+"&xmname="+this.goods.xmname;
        url+="&price="+this.goods.price+"&vs="+this.goods.vs+"&color="+this.goods.color+"&uname="+sessionStorage.getItem("uname");;
        this.axios.get(url).then(result=>{
          console.log(result)
        })
        this.$router.push(`/shopCart/${this.lid}`);
      }else{//跳转到登录页面
        sessionStorage.setItem("prevPath",this.$route.path);
        this.$router.push("/login");
      }

    },
    // 选择颜色
    colorClick(colori){
      // 初始化 轮播图的为 第一张图片
      // 获取 总的 图片数
      // var srcLength = this.data[0].src1.length;
      // console.log(srcLength)
      // 不同颜色的 第一张 图
      this.img_index=colori;
      this.moveCnt=colori * 4;

      clearInterval(this.init1);
      this.init1=null;
      this.shift_pic();

      this.show_src=this.goods.src[this.moveCnt];
      this.auto_move();
      // 
      var colorlis=document.querySelectorAll(`.show8>li`);
      // console.log(colorlis)
      for(var colorli of colorlis){
        colorli.style.borderColor="#DDD";
        colorli.style.color="#000";
      }
      document.querySelector(`.show8>li:nth-child(${colori+1})`).style.borderColor="#FF6700";
      document.querySelector(`.show8>li:nth-child(${colori+1})`).style.color="#FF6700";

      this.goods.color=this.data[this.lid].color[colori];
    },
    // 选择版本
    vsClick(vsi){
      // console.log(vsi)
      // 版本号
      
      var vslis = document.querySelectorAll(`.show6>li`);
      // console.log(vslis)
      for(var vsli of vslis){
        vsli.style.borderColor="#DDD";
        vsli.style.color="#000";
      }
      
      document.querySelector(`.show6>li:nth-child(${vsi+1})`).style.borderColor="#FF6700";
      document.querySelector(`.show6>li:nth-child(${vsi+1})`).style.color="#FF6700";
      this.goods.vs=this.data[this.lid].vs[vsi];
      // 版本号对应的价格
      this.goods.price=this.data[this.lid].price[vsi];
      // console.log(this.goods.price)
    },
    // 定时器 轮播
    shift_pic(){
      var cnt=0;
      var parent=this;
      if(this.init1==null){
        this.init1=setInterval(function(){
          cnt++;
          if(cnt==4){
            cnt=0;
            parent.moveCnt++;
            if(parent.moveCnt > 4 * (parent.img_index+1) - 1){
              parent.moveCnt=4 * parent.img_index;
            }
            // 轮播条
            parent.auto_move();
          }
          parent.show_src=parent.goods.src[parent.moveCnt];
        },1000)
      }
    }
  },
  mounted() {
    this.show();
    this.shift_pic();
  },
  watch: {
    show_src(){
      // this.shift_pic();
    }
  },

}
</script>
<style scoped>
/***1**** */
  .shopping{
    background:#fff;
  }
  .box_details{
    display:flex;
    justify-content:space-between;
    padding-bottom:100px;
  }
  .show_pic,.fix{
    width:606px;
    position:relative;
    height:600px;
  }
  /* 轮播 下图标 */
  .show_pic>ul{
    display:flex;
    position:absolute;
    width:200px;
    top:550px;
    left:170px;
    justify-content:space-between;
  }
  .show_pic>ul>li{
    width:20%;height:3px;
    background:#ccc;
    border-radius:20px;
  }
  .show_pic>ul>li:hover,
  .show_pic>span:hover,  
  .show_details>.show6>li:hover,
  .show_details>.show8>li:hover,
  .show_details>.show10>li:hover,
  .show12>li:hover{
    cursor:pointer;
  }
  .show_pic>ul>li:nth-child(1){
    background:#999;
  }
  /* 轮播按钮 */
  .show_pic>span:nth-child(2){
    left:0px;
  }
  .show_pic>span:nth-child(3){
    left:510px;
    // color:#f00;
  }
  .show_pic>span:hover{
    background:rgba(0,0,0,0.2);
  }
  .show_pic>span{
    top:230px;
    position:absolute;
    color:#929292;
    font-size:30px;
    width:50px;height:70px;
    // background:#ddd;
    line-height:70px;
    text-align:center;
  }
  .show_pic>img{
    transition:2s;
    width:560px;
    height:560px;
  }
  .show_pic::after,.fix::after{
    content:"";
    display:table;
    clear:both;
  }
  .show_details{
    top:0px;left:0px;
    width:606px;

  }
/***2************* */
  .show_details>.show1{
    font-weight:500;
    font-size:24px;
  }

  .show_details>.show3,
  .show_details>.show11>ul>li:nth-child(2){
    color:#FF6700;
  }
  .show_details>.show4>span:nth-child(1){
    display:inline-block;
    width:80px;height:25px;
    text-align:center;
    line-height:25px;
    background:#FF6700;
    color:#fff;
  }
  .show_details>.show4{
    border:1px solid #ddd;
    padding:15px 0;
    border-left-color:transparent;
    border-right-color:transparent;
  }
  .show_details>.show4>span:nth-child(2){
    font-size:14px;
    margin-left:10px;
  }
  .show_details>.show5,
  .show_details>.show7,
  .show_details>.show9{
    margin-top:20px;
    padding:15px 0;
    font-size:18px;
  }
  .show_details>.show6,
  .show_details>.show8,
  .show_details>.show10{
    display:flex;
    flex-wrap:wrap;
    justify-content:space-between;
  }
  

  .show_details>.show6>li,
  .show_details>.show8>li,
  .show_details>.show10>li{
    width:49%;
    border:1px solid #ddd;
    padding:10px 0;
    margin-top:10px;
    text-align:center;
  }
  .show_details>.show11{
    height:140px;
    background:#f9f9f9;
    padding:30px 0;
    margin:30px 0;
  }
  .show_details>.show11>ul{
    position:relative;
    padding:10px 30px;
    top:-10px;left:0px;
  }
  .show_details>.show11>ul>li:nth-child(1)>span{
    // margin-left:256px;
    // padding:0 10px;
    position:absolute;
    top:20px;left:456px;
  }
  .show_details>.show11>ul>li:nth-child(1){
    padding:10px 0;
    font-size:14px;
    color:#616161;
  }
  .show_details>.show11>ul>li:nth-child(2){
    font-size:24px;
    margin:10px 0;
  }
  .show12{
    height:56px;
    display:flex;
  }
  .show12>li{
    color:#fff;
    line-height:54px;
    text-align:center;
  }
  .show12>li:nth-child(1){
    width:300px;height:54px;
    background:#FF6700;
    margin-right: 15px;
  }
  .show12>li:nth-child(1):hover{
    background:#f25807;
  }
  
  .show12>li:nth-child(2):hover{
    background:#757575;
  }
  .show12>li:nth-child(2){
    width:142px;height:54px;
    background:#B0B0B0;
  }
  .show12>li:nth-child(1),
  .show12>li:nth-child(2){
    transition:1s;
  }
/***3*************** */
  .show_details>.show6>li:active{
    color:#FF6700;
  }
  .show6>li:nth-child(1),.show8>li:nth-child(1),.show10>li:nth-child(1){
    border-color:#FF6700;
    color:#FF6700;
  }
</style>