<template>
  <div >
    <!--<div></div>-->
    <div class="loginHead">
      <div class="container">
        <img src="img/reg--login/xmLogo.png">
        <div class="login_box">
          <span class="loginMsg" v-if="loginFlag==1">登录成功，正在跳转</span>
          <ul>
            <li><span>账号登录</span>|<span>扫码登录</span></li>
            <li><input type="text" placeholder="邮箱/手机号码/小米ID" v-model="uname"></li>
            <li>
              <input type="text" placeholder="密码" v-model="upwd">
              
            </li>
            <li> 
              <p class="test">
                <input type="text" @focus="enterTest" v-model="inCode" placeholder="图片验证码"> 
                <span @click="beTest">{{testCode}}</span>
              </p>
              <p class="msg" v-if="inputIndex!=0"><span>!</span>{{showMsg}}</p>
              <button @click="btnSubmit">登录</button>
              <div>
                <span>手机短信登录/注册</span>
                <div>
                  <span @click="regClick">立即注册</span>|<span>忘记密码</span>
                </div>
              </div>

            </li>
           
          </ul>
        </div>
      </div>
    </div>
    <div class="pic1">
      <img src="img/reg--login/pic1.jpg">
      
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      uname:"",
      upwd:"",
      testCode:"",
      inCode:"",
      showMsg:"",
      inputIndex:"",
      loginFlag:0
    }
  },
  created() {
    this.load();
  },
  methods: {
    // 加载页面
    load(){
      // 重加载一次
      if(sessionStorage.getItem("reloadFlag")==1){
        sessionStorage.setItem("reloadFlag",0);
        window.location.reload();
      }
      this.testCode=this.FnTestCode();
    },
    regClick(){
        sessionStorage.setItem("prevPath",this.$route.path);
        sessionStorage.setItem("reloadFlag",1);
        this.$router.push("/reg");

    },
    enterTest(){//验证码输入宽 focus 事件
      if(this.inputIndex==3){
        this.showMsg="";
        this.inputIndex=0;// 判断是否有提示 ！
      }
    },
    // 生成随机验证码
    FnTestCode(){
      // 48-57  65-90  97-122
      // 0-9    A-Z    a-z
      var codeAll="";
      for(var i=0;i<4;i++){
        var code = parseInt(Math.random()*62);
        if(code<10){
          code = code+48;
        }else if(code<36){
          code = code + 65 - 10;
        }else{
          code = code + 97 - 36;
        }
        code = String.fromCharCode(code);
        codeAll+=code;
      }
      return codeAll;
    },
    beTest(){
      // 点击切换验证码
      this.testCode=this.FnTestCode();
      console.log(this.testCode)

    },
    
    btnSubmit(){//点击按钮，登录提交
      if(!this.uname){
        this.showMsg="请输入账号";
        this.inputIndex=1;
        return;
      }
      if(!this.upwd){
        this.showMsg="请输入密码";
        
        this.inputIndex=2;
        return;
      }
      if(this.testCode.toUpperCase()!=this.inCode.toUpperCase()){
        this.showMsg="验证码不正确";
        this.inputIndex=3;
        return;
      }
      var url="http://binyuailly.github.io/xm/login?"
      url+="uname="+this.uname+"&upwd="+this.upwd;
      // console.log(url)
      this.axios.get(url).then((result)=>{
        console.log(result.data)
        // result.data=JSON.parse(result.data)
        if(result.data.code==1){
          this.loginFlag=1;
          // 保存登录状态
          sessionStorage.setItem("uname",this.uname);
          sessionStorage.setItem("loginFlag",this.loginFlag);
          sessionStorage.setItem("reloadFlag",1);
          console.log(this.loginFlag);
          var parent=this; 
          setTimeout(function(){
            var path=sessionStorage.getItem("prevPath");
            console.log(path);
            // if(path==null||path=="/reg"){
              parent.$router.push("/");
            // }
            // else{
              // parent.$router.push(`${path}`);
            // }
            
          },500)
        }else{
          this.loginFlag=0;
          this.showMsg=result.data.msg;
          this.inputIndex=10;//显示 msg 的条件  inputIndex != 0;

          console.log(this.loginFlag,this.showMsg,this.$route.path)
        }
      })
    }
  },
  mounted() {
    
  },
  watch: {
    uname(){
      console.log(this.uname);
    },
    upwd(){
      console.log(this.upwd);
    }
  },
}
</script>
<style scoped>
  .container{
    // position:relative;
  }
  .loginHead{
    background:#fff;
  }
  .pic1{
    overflow:hidden;
    background:#bf0007;
    height:598px;
    
  }
  .pic1>img{
    width:1381px;
    height:598px;
    margin-left:-200px;
  }
  .login_box{
    position:absolute;
    top:174px;
    margin-left:700px;
    width:410px;
    height:524px;
    background:#fff;
    // text-align:center;
  }
  .login_box>ul>li:nth-child(1){
    margin-top:30px;
  }
  .login_box>ul>li:nth-child(1){
    font-size:28px;
    color:#ddd;

  }
  .login_box>ul>li:nth-child(1)>span:nth-child(1){
    color:#ff6700;
  }
  .login_box>ul>li:nth-child(1)>span:hover{
    // cursor:pointer;
    color:#ff6700;
  }
  .login_box>ul>li:nth-child(1)>span{
    margin:0 40px;
    font-size:24px;
    color:#666;
    
  }
  .login_box>ul>li{
    width:346px;
    height:50px;
    margin:20px auto;
  }
  .login_box>ul>li>input{
    height:50px;
    width:346px;
    border-radius:0px;
    
  }
  input::-webkit-input-placeholder,
  .login_box>ul>li>input{
    color:#999;
    font-size:14px;
  }
  .login_box>ul>li>button{
    height:50px;
    width:346px;
    color:#fff;
    border-color:transparent;
    border-radius:0px;
    background:#ff6700;
  }
  .login_box>ul>li:nth-child(4)>p{
    // margin:10px 0px;
    // border:1px solid #ddd;
  }
  .login_box>ul>li:nth-child(4)>.msg{
    color:#ff6700;
  }
  .login_box>ul>li:nth-child(4)>.msg>span{
    position:relative;
    display:inline-block;
    color:#fff;
    font-size:14px;
    width:14px;
    height:14px;
    text-align:center;
    line-height:15px;
    background:#ff6700;
    border-radius:50%;
    margin-right:4px;
  }
  .login_box>ul>li:nth-child(4)>.test{
    // line-height:68px;
    // align-items:center;
  }
  .login_box>ul>li:nth-child(4)>.test>span{
    position:absolute;
    line-height:55px;
    text-align:center;
    width:155px;height:55px;
    // border:1px solid #ddd;
  }
  .login_box>ul>li:nth-child(4)>.test>input{
    width:55%;
    height:55px;
    margin:0 ;
    border-radius:0px;
  }
  .login_box>ul>li:nth-child(4)>div{
    position:relative;
    top:15px;
    display:flex;
    justify-content:space-between;
  }
  .login_box>ul>li:nth-child(4)>div{
    font-size:16px;
  }
  .login_box>ul>li:nth-child(4)>div>span:hover,
  .login_box>ul>li:nth-child(4)>div>div>span:hover,
  .login_box>ul>li:nth-child(4)>.test>span:hover,
  .login_box>ul>li:nth-child(1)>span:hover{
    cursor:pointer;
  }
  .login_box>ul>li:nth-child(4)>div>div>span:hover{
    color:#ff6700;
  }
  .login_box>ul>li:nth-child(4)>div>span{
    color:#ff6700;
  }
  .login_box>ul>li:nth-child(4)>div>div>span{
    margin:0px 5px;
  }
  .login_box>ul>li:nth-child(4)>div>div,
  .login_box>ul>li:nth-child(4)>div>div>span{
    color:#999;
    font-size:15px;
  }
  .login_box>.loginMsg{
    display:inline-block;
    position:absolute;
    z-index:10;
    text-align:center;
    line-height:300px;
    overflow:hidden;
    top:180px;left:100px;
    width:180px;height:180px;
    color:#fff;
    font-size:20px;
    background:rgba(0,0,0,0.5);

  }
</style>