<template>
  <div>
    <div class="container">
      <div class="logo_xm">
        <img src="img/reg--login/xmFirst.png">
      </div>
      <h2 class="regTitle">注册ailly帐号</h2>
      <div class="reg_box">
        <h4>用户名</h4>
        <input type="text" v-model="uname">
        <span>密码</span>
        <input type="text" v-model="upwd">
        <span>手机号</span>
        <input type="text" v-model="phone">
        <span>邮箱</span>
        <input type="text" v-model="email">
        <p v-if="showFlag==1"><span  >!</span>{{showMsg}}</p>
        <button @click="submitbtn">立即注册</button>
        
      </div>
      <div class="Tic" @click="bgAgree"><span class="sel">✔</span>注册帐号即表示您同意并愿意遵守ailly <span>用户协议</span>和 <span>隐私政策</span></div>
    
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      uname:"",upwd:"",phone:"",
      email:"",bgFlag:0,
      showMsg:"",showFlag:0
    }
  },
  created() {
  },
  methods: {
    load(){
      // 重加载一次
      if(sessionStorage.getItem("bgFlag")==null){
        sessionStorage.setItem("bgFlag",0)
      }
      if(sessionStorage.getItem("reloadFlag")==1){
        sessionStorage.setItem("reloadFlag",0);
        window.location.reload();
      }
      if(sessionStorage.getItem("bgFlag")==1){
        document.querySelector(".Tic>span").style.background="#fff";
      }else{
        document.querySelector(".Tic>span").style.background="#ff6700";
      }

    },
    bgAgree(){
      var bg=sessionStorage.getItem("bgFlag");
      bg=1-bg;
      sessionStorage.setItem("bgFlag",bg);//bg ==1 未同意, ;bg ==0 ,已同意
      if(bg==1){
        document.querySelector(".Tic>span").style.background="#fff";
      }else{
        document.querySelector(".Tic>span").style.background="#ff6700";
      }
      console.log(document.querySelector(".Tic>span").style.background)
    },
    submitbtn(){//注册提交
      var testName=/^[A-Z0-9a-z-\u4e00-\u9fa5]{2,10}$/i;
      var testPwd=/^[A-Z0-9a-z]{6,12}$/i;
      var testPhone=/^1[3-8]\d{9}$/i;
      var testEmail=/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)$/i;
      this.bgFlag=sessionStorage.getItem("bgFlag");
      if(this.bgFlag==1){//没有同意协议
        this.showMsg="请您同意用户条款";
        this.showFlag=1;
        // return;
      }
      else if(this.uname==""||!testName.test(this.uname)){
        this.showMsg="请输入规范的用户名";
        this.showFlag=1;
        // return;
      }
      else if(this.upwd==""||!testPwd.test(this.upwd)){
        this.showMsg="请输入规范的密码";
        this.showFlag=1;
        // return;
      }
      else if(this.phone==""||!testPhone.test(this.phone)){
        this.showMsg="请输入格式正确的电话";//请输入格式正确的电话
        this.showFlag=1;
        // return;
      }
      else if(this.email==""||!testEmail.test(this.email)){
        this.showMsg="请输入格式正确的邮箱";//请输入格式正确的邮箱
        this.showFlag=1;
        // return;
      }else{
        this.showFlag=0;
      }
      if(this.showFlag==0){
        var url="http://binyuailly.github.io/xm/reg?"
        url+="uname="+this.uname+"&upwd="+this.upwd;
        url+="&phone="+this.phone+"&email="+this.email;
        this.axios.get(url).then(result=>{
          // console.log(result)
          if(result.data.code==1){
            sessionStorage.setItem("reloadFlag",1);
            this.$router.push("/login");
          }
        })
      }
    }
  },
  mounted() {
    this.load();
  },
  // watch: {
  //   uname(){
  //     console.log(this.uname)
  //   }
  // }
}
</script>
<style scoped>
  .container{
    margin-top:50px;
    width:854px;
    padding:30px;
  }
  .logo_xm,.regTitle,.Tic{
    text-align:center;
  }
  .regTitle{
    font-weight:500;
    margin:30px 0px;
  }
  .reg_box{
    width:332px;
    height:319px;
    margin:0 auto;
  }
  .reg_box{
    font-size:14px;

  }
  .reg_box>h4{
    font-size:14px;
    font-weight:500;
  }
  .reg_box>button:hover{
    background: #ff936a;
  }
  .reg_box>button{
    display:inline-block;
    width:332px;height:42px;
    background:#ff6700;
    border:0px solid #fff;
    border-radius:0px;
    outline:none;
    color:#fff;
  }
  .reg_box>p>span{
    display:inline-block;
    width:15px;height:15px;
    line-height:15px;
    margin-right:4px;
    background:#ff6700;
    text-align:center;
    border-radius:50%;
    color:#fff;
  }
  .reg_box>p{
    color:#ff6700;
  }
  .Tic:hover{
    cursor:pointer;
  }
  .Tic{
    font-size:14px;
    width:420px;
    margin:0 auto;
    margin-top:100px ;

  }
  .Tic>.sel{
    display:inline-block;
    font-size:14px;
    color:#fff;
    background:#ff6700;
    width:20px;height:20px;
    border:1px solid #ddd;
    border-radius:50%;
    margin-right:7px;
  }
  .Tic>span:not(:nth-child(1)){
    font-size:15px;
    font-weight:1000;
  }
</style>
