<template>
  <div class="bigBanner">
    
      <img :src="src[moveCnt]">
      <span @click="leftMove">&lt;</span>
      <span @click="rightMove">&gt;</span>
      <ul>
        <li @click="indexClick(0)"></li>
        <li @click="indexClick(1)"></li>
      </ul>
  </div>
</template>
<script>
export default {
  data() {
    return {
      moveCnt:0,
      src:["img/banner/banner1.jpg","img/banner/banner2.jpg"]
    }
  },
  methods: {
    load(){

    },
    rightMove(){
      this.moveCnt++;
      if(this.moveCnt>1){
        this.moveCnt=0;
      }
      var moveli = document.querySelectorAll(".bigBanner>ul>li");
      // console.log(moveli)
      if(moveli!=null){
        for(var li of moveli){
          li.style.background="#999";
        }
        moveli[this.moveCnt].style.background="#ff6700"

      }
    },
    leftMove(){
      this.moveCnt--;
      if(this.moveCnt<0){
        this.moveCnt=1;
      }
      var moveli = document.querySelectorAll(".bigBanner>ul>li");
      // console.log(moveli)
      if(moveli!=null){
        for(var li of moveli){
          li.style.background="#999";
        }
        moveli[this.moveCnt].style.background="#ff6700"

      }
    },
    indexClick(index){
      // console.log(index)
      this.moveCnt=index;
      var moveli = document.querySelectorAll(".bigBanner>ul>li");
      if(moveli!=null){
        for(var li of moveli){
          li.style.background="#999";
        }
        moveli[index].style.background="#ff6700"
      }
      
    }
    
  },
  mounted() {
    this.load();
  },
}
</script>
<style scoped>
  .bigBanner{
    position:relative;
    height:620px;
    width:1226px;
    // background:#0af;
    margin:0 auto;
  }
  .bigBanner>img{
    width:1926px;
    position:relative;
    left:-350px;top:10px;
    height:620px;
  }
  .bigBanner>span{
    font-size:32px;
    position:absolute;
    top:10px;
  }
  .bigBanner>span:nth-child(3){
    left:100%;
  }
  .bigBanner>span:nth-child(2):hover,
  .bigBanner>span:nth-child(3):hover{
    cursor:pointer;
    background:rgba(0,0,0,0.5);
    color:#fff;
  }
  .bigBanner>span:nth-child(2),
  .bigBanner>span:nth-child(3){
    top:40%;
    display:inline-block;
    width:40px;height:70px;
    line-height:70px; 
    text-align:center;
    color:#ddd;
  }
  .bigBanner>ul{
    position:absolute;
    top:95%;left:45%;
    width:40px;
    display:flex;
    justify-content:space-between;
  }
  .bigBanner>ul>li:hover{
    cursor:pointer;
  }
  .bigBanner>ul>li{

    width:10px;height:10px;
    background:#999;
    border-radius:50%;
  }
  .bigBanner>ul>li:nth-child(1){
    background:#ff6700;
  }
</style>
