<template>
  <div>
    <div class="toPay">
      <div class="container">
      <!--<div></div>-->
        <div class="payHead1">
          <img src="img/reg--login/xmFirst.png" @click="jumpFirst">
          <span>确认订单</span>
        </div>
        <div class="payHead2">
          <div class="show" @mouseleave="hide">
            <span @mouseenter="show" >{{user}}
              <span>&lt;</span></span>
            <ul v-if="showFlag==1">
              <li>个人中心</li>
              <li>评价晒单</li>
              <li>我的喜欢</li>
              <li>小米账号</li>
              <li >退出登录</li>
            </ul>
          </div>
          |<span>我的订单</span>
          
        </div>
      </div>

    </div>
    <div class="toPayContent">
      <div class="container">
        <h3 class="myIP">收货地址</h3>
        <div class="addIP">
          <div class="IpChild" @mouseenter="addIpEnter" @mouseleave="addIpleave"  @click="ToastMsg($event)">
            <div class="IpChildDiv">
              <span class="IpChildDivSpan">+</span>
              <p class="IpChildDivP">添加新地址</p>
            </div>
          </div>

        </div>
        <h3 class="myHopePayTitle">商品及优惠券</h3>
        <ul class="myHopePay">
          <li v-for="(item,i) of payData" :key="i">
            <img :src="item.pic"/>
            <span>{{item.xmname}} {{item.color}}</span>
            <span>{{item.price}}元 x {{item.num}}</span>
            <span>{{item.price * item.num}}元</span>
          </li>
        </ul>
        <div class="sendHow">
          配送方式
          <span>包邮</span>
        </div>
        <div class="invoice">
          发票
          <span>电子发票</span>
          <span>个人</span>
          <span>商品明细</span>
          <span>修改 &gt;</span>
        </div>

        <div class="payDetails">
          <div>
            <p> <span>+</span> 使用优惠券 </p>
            <p> <span>+</span> 使用小米礼品卡 </p>
          </div>
          <div>
            <ul>
              <li>
                <span>商品件数：</span><span>{{num}}件</span>
              </li>
              <li>
                <span>商品总价：</span><span>{{total}}元</span>
              </li>
              <li>
                <span>活动优惠：</span><span>-0元</span>
              </li>
              <li>
                <span>优惠券抵扣：</span><span>-0元</span>
              </li>
              <li>
                <span>运费：</span><span>-0元</span>
              </li>
              <li>
                <span>应付总额： </span> 
                <span>{{total}} <span>元</span> </span>
              </li>
            </ul>
          </div>
        </div>
        <div class="OkPay">
          <button @click="backCart">返回购物车</button>
          <button class="OkPayBtn" @mouseenter="ToastMsg($event)">去结算</button>
        </div>
        
      </div>
    </div>


  <span class="msgToast"> {{msg}}</span>
  </div>
</template>
<script>
export default {
  data() {
    return {
      showFlag:0,
      user:"",
      payData:[],
      total:0,
      num:0,
      msg:"msg",
      msgTime:null
    }
  },
  methods: {
    load(){
      
      var parent=this;
      if(sessionStorage.getItem("reloadFlag")==1){
        sessionStorage.setItem("reloadFlag",0);
        window.location.reload();
      }
      var url="http://binyuailly.github.io/xm/toPay?"
      url+="uname="+sessionStorage.getItem("uname");

      this.axios.get(url).then(result=>{
        console.log(result);
        if(result.data.length>0){
          this.payData=result.data;
          this.total=0;
          this.num=0;
          for(var i=0;i<this.payData.length;i++){
            this.total+=this.payData[i].price * this.payData[i].num;
            this.num+=this.payData[i].num;
          }
        }
      })
      this.user=sessionStorage.getItem("uname");
        
      /********************************************************************* */
 
      /************************************************************** */

        // this.mySelFun();
    },
    ToastMsg(e){
      // Toast("此功能")
      if(this.msgTime==null)
      this.msgTime = setTimeout(()=>{
        var msgSpan = document.querySelector(".msgToast");
        console.log(msgSpan)
        this.msg="此功能暂无";
        msgSpan.style.display="block"
        // console.log(e.clientY,e.clientX,e.target.className)
        var msgElement=document.querySelector(`.${e.target.className}`);
        console.log(msgElement)
        var top = msgElement.getBoundingClientRect().top;
        var left =e.clientX - msgElement.getBoundingClientRect().width/2;
        msgSpan.style.top=top + 50 + "px";
        msgSpan.style.left=left + "px";
        setTimeout(()=>{
          msgSpan.style.display="none";
          this.msgTime=null;
        },1000)
      },1)
    },
    addIpEnter(){
      var addIpDivP=document.querySelector(".IpChild>div>p");
      var addIpDivSpan=document.querySelector(".IpChild>div>span");
      addIpDivSpan.style.background="#aaa"
      addIpDivP.style.color="#666"
      
    },
    addIpleave(){
      var addIpDivP=document.querySelector(".IpChild>div>p");
      var addIpDivSpan=document.querySelector(".IpChild>div>span");
      addIpDivSpan.style.background=""
      addIpDivP.style.color=""

    },
    // 返回购物车页面
    backCart(){
      sessionStorage.setItem("reloadFlag",1);
      this.$router.push("/pay")

    },
    jumpFirst(){
      sessionStorage.setItem("reloadFlag",1);
      this.$router.push("/")
    },
    show(){
      this.showFlag=1;
    },
    hide(){
      this.showFlag=0;
    }
  },
  created() {
    // this.load();
  },  
  mounted() {
    this.load();
  },
  
}
</script>
