<template>
<div>
  <div class="home">
    <div class="BGH container">
      <ul class="Mheader">
        <li><span>ailly商城</span><span>|</span></li>
        <li><span>MIUI</span><span>|</span></li>
        <li><span>IoT</span><span>|</span></li>
        <li><span>云服务</span><span>|</span></li>
        <li><span>金融</span><span>|</span></li>
        <li><span>有品</span><span>|</span></li>
        <li><span>小爱开放平台</span><span>|</span></li>
        <li><span>政企服务</span><span>|</span></li>
        <li><span>资质证照</span><span>|</span></li>
        <li><span>协议规则</span><span>|</span></li>
        <li><span>下载app</span><span>|</span></li>
        <li><span>Select Region</span></li>
      </ul>
      <ul class="pReg">
        <li @click="loginClick">
          <span v-if="loginFlag!=1">登录</span>
          <div  class="login_ed" v-if="loginFlag==1" @mouseenter="showUser" @mouseleave="hideUser">
            <span>
              {{uname}} <span>&lt;</span>
            </span>
            <ul v-if="show_user==1">
              <li>个人中心</li>
              <li>评价晒单</li>
              <li>我的喜欢</li>
              <li>小米账号</li>
              <li @click="quitLogin">退出登录</li>
            </ul>
          </div>
          <span>|</span>
          
        </li>
        <li v-if="loginFlag!=1"><span @click="regClick">注册</span><span>|</span></li>
        <li><span>消息通知</span><span>|</span></li>
        <li><span>我的订单</span><span>|</span></li>
        <li @mouseenter="showCart" @mouseleave="hideCart">
          <div>
            <span>购物车</span>
            <p v-if="addCart>0&&loginFlag==1">({{addCart}})</p>
          </div>  
          <ul class="showDetail" v-if="showCartFlag==1">
          <!--showCartFlag==-->
            <li v-if="addCart>0&&loginFlag==1" v-for="(item,i)  of showdata" :key="i" @mouseenter="enterList" @mouseleave="leaveList" >
              <img :src="item.pic">
              <span>{{item.xmname}} {{item.vs}} {{item.color}}</span>
              <span class="price">{{item.price}} X {{item.num}}</span>
              <span class="close" v-if="enterListFlag==1" @click="closeClick(i,item.num,item.xmname)">X</span>
            </li>
            <li v-if="addCart>0&&loginFlag==1" class="liToPay">
              <div>
                <span> 共 {{addCart}} 件商品</span>
                <p> {{total}}<span>元</span></p>
              </div>
              <button @click="payClick">去购物车结算</button>
            </li>
            <li class="without" v-if="addCart==0||loginFlag==0">
              <span> 购物车中还没有商品，赶紧选购吧！</span>
            </li>

          </ul>
        </li>
      </ul>
    </div>
    <div id="hideScrollBottom"></div>
  </div>
  <div class="clsHead">
    <div class="container">
      <div class="titleHead">
        <ul>
          <li @click="firstClick">
            <img src="img/reg--login/xmFirst.png">
          </li>
          <li>全部商品分类</li>
          <li>小米手机</li>
          <li>红米</li>
          <li>电视</li>
          <li>笔记本</li>
          <li>家电</li>
          <li>新品</li>
          <li>路由器</li>
          <li>智能硬件</li>
          <li>服务</li>
          <li>社区</li>
        </ul>
        <input type="text" >
        <span>
          <span></span>
          <span></span>
        </span>
        
      </div>
    </div>
  </div>
  <router-view></router-view>
</div>
</template>
<script>
export default {
  data() {
    return {
      loginFlag:0,
      uname:"",
      show_user:0,
      addCart:0,
      showCartFlag:0,
      showdata:[],
      total:0,
      enterListFlag:0
    }
  },
  created() {
    this.load();
  },
  methods: {
    load(){
      if(sessionStorage.getItem("reloadFlag")==1){
        sessionStorage.setItem("reloadFlag",0);
        window.location.reload();
      }
      
      // 
      var url="http://binyuailly.github.io/xm/showcart?";
      url+="uname="+sessionStorage.getItem("uname");
      this.axios.get(url).then(result=>{
        // console.log(result)
        var num=0;
        for(var i=0;i<result.data.length;i++){
          this.showdata[i]=result.data[i];
          num+=result.data[i].num;
          this.total+=result.data[i].num*result.data[i].price;
        }
        sessionStorage.setItem("addCart",num);
        // this.showdata= result.data;
        console.log(this.showdata)
      })
      // sessionStorage.setItem("addCart",null)
      this.addCart=sessionStorage.getItem("addCart");
      console.log("addCart"+this.addCart)
      // 加入购物车的商品个数
      

      this.loginFlag=sessionStorage.getItem("loginFlag");
      console.log("login:"+this.loginFlag)
      this.uname=sessionStorage.getItem("uname");
      if(this.loginFlag==1){
        setTimeout(function(){//等待页面加载完成 后才能获取到span标签
          // console.log(document.querySelector(".pReg>li:nth-child(1)>span:nth-child(1)"))
          var userSpan=document.querySelector(".pReg>li:nth-child(1)>div:nth-child(1)");
          userSpan.style.display="inline-block";
          userSpan.style.width="125px";
          userSpan.style.right="215px";
        },1)
      }

      
    },
    // pay
    payClick(){
      sessionStorage.setItem("reloadFlag",1);
      this.$router.push("/pay")
    },
    closeClick(i,num,xmname){
      console.log(i)
      this.addCart=this.addCart-num;
      sessionStorage.setItem("addCart",this.addCart);
      var url="http://binyuailly.github.io/xm/del?xmname="+xmname;
      url+="&color="+this.showdata[i].color;
      url+="&uname="+sessionStorage.getItem("uname");
      url+="&vs="+this.showdata[i].vs;
      this.axios.get(url).then(result=>{
        console.log(result)
      })
      // 先查找，后删除
      this.showdata.splice(i,1);
      window.location.reload();
    },
    enterList(){
      this.enterListFlag=1;
    },
    leaveList(){
      this.enterListFlag=0;
    },
    hideCart(){
      console.log(22);
      this.showCartFlag=0;
      // var hideSB=document.querySelector("#hideScrollBottom");
      // hideSB.className=""
      var div=document.querySelector(".pReg>li:last-child>div");
      var p=document.querySelector(".pReg>li:last-child>div>p");
      if(div!=null){//防止报错
        div.className=""
      }
      if(p!=null){
        p.className=""
      }
      
    },
    showCart(){
      console.log(11);
      this.showCartFlag=1;
      var div=document.querySelector(".pReg>li:last-child>div");
      var p=document.querySelector(".pReg>li:last-child>div>p");//offsetHeight
      if(div!=null){
        div.className="myCart"
      }
      if(p!=null){
        p.className="myCart"
      }
      setTimeout(()=>{
        var listHeight=document.querySelector(".showDetail");
        var listBtn=document.querySelector(".showDetail>li:last-child");
        // var hideSB=document.querySelector("#hideScrollBottom");
          // hideSB.className="hideScrollBottom"
        if(listHeight.offsetHeight > 400){
          // 判断购物车 是否 超过规定 高度
          listHeight.style.height=400+"px";
          // listHeight.style.overflowX="hidden";
          listHeight.style.overflowY="auto";
          // 固定购物按钮
          listBtn.style.position="fixed";
          listBtn.style.zIndex="10000";
          listBtn.style.top="440px";
          listBtn.style.background="#fff";
          listBtn.style.width="415px";
          // hideSB.className="hideScrollBottom"
          // listBtn.style.left="360px";

        }else{
          listHeight.style.height=listHeight.offsetHeight+"px";
          
        }
        console.log(listHeight)

      },1)
    },
    firstClick(){//首页
      sessionStorage.setItem("reloadFlag",1);
      this.$router.push("/");
    },
    regClick(){
        sessionStorage.setItem("prevPath",this.$route.path);
        sessionStorage.setItem("reloadFlag",1);
        // this.$router.push("/reg");
        // 打开 新窗口
        var XiaoMiUrl=sessionStorage.getItem("XiaoMiUrl")
        window.open(`${XiaoMiUrl}reg`)
        // https://binyuailly.github.io/XiaoMi/dist/#/
    },
    quitLogin(){//退出登录
      var parent=this;
      setTimeout(function(){
        parent.loginFlag=0;
        sessionStorage.setItem("loginFlag",parent.loginFlag);
        window.location.reload();
      },100)
      var userSpan=document.querySelector(".pReg>li:nth-child(1)>span:nth-child(1)")
      userSpan.style.display="";
      userSpan.style.width="";
    },
    showUser(){
      this.show_user=1;
      var show = document.querySelector(".login_ed");
      var showSpan = document.querySelector(".login_ed>span");

      show.style.background="#fff";
      show.style.boxShadow="0 0 2px #ddd";
      showSpan.style.color="#333";
      
    },
    hideUser(){
      this.show_user=0;
      var show = document.querySelector(".login_ed");
      var showSpan = document.querySelector(".login_ed>span");
      show.style.background="";
      show.style.boxShadow="";
      showSpan.style.color="#b0b0b0";
    },
    loginClick(){
      if(this.loginFlag!=1){
        sessionStorage.setItem("prevPath",this.$route.path);
        sessionStorage.setItem("reloadFlag",1);

        this.$router.push("/login");
        // window.open("http://localhost:8080/#/login")
      }
    }
  },
  mounted() {
    
  },
}
</script>
<style scoped>
  #hideScrollBottom{
    position:fixed;
    z-index:100000;
    top:0px;left:910px; 
    width:414px;
