    color:#333;
    right:0px;top:41px;
    width:416px;
    // 
    padding:10px 0;
    background:#fff;
    border:1px solid #ddd;
    // box-shadow:0 10 1px #ddd;
    border-top-color:transparent;
    border-right-color:transparent;
  }
  .pReg>li:nth-child(1){
    // position:relative;
  }
/*   position:absolute;
     right:105px;
     background:#fff;*/
  .pReg>li:nth-child(1)>span:nth-child(1){
    text-align:center;
  }
  .BGH{
    /* 固定登录后的 用户box */
    position:relative;
  }
  .Mheader>li>span:nth-child(1):hover,.pReg>li:hover{
    cursor:pointer;
    color:#fff;
  }
  .Mheader>li>span:nth-child(2),.pReg>li>span:nth-child(2){
    margin:0 .5em;
    font-size:12px;
