  }
  .titleHead>input{
    outline:none;
    border:1px solid #ddd;
    border-radius:0px;
  }
  .titleHead>span:hover{
    cursor:pointer;
  }
  .titleHead>span>span{
    display:block;
    position:relative;
  }
  .titleHead>span>span:nth-child(1){
    width:15px;height:15px;
    border-radius:50%;
    border:2px solid #616161;
    top:12px;left:12px;
  }
  .titleHead>span>span:nth-child(2){
    width:3px;height:10px;
    background:#616161;
    transform:rotate(-45deg);
    top:8px;left:25px;
  }
  .titleHead>span{
    display:block;
    position:relative;
    line-height:50px;
