  }
  .login_ed{
    position:absolute;
    z-index:10;
    width:110px;
    padding:10px 0;
    text-align:center;
    top:0px;right:110px;
  }
  .login_ed>ul>li{
    height:30px;
    line-height:30px;
    transition:0.2s;
    color:#666;
  }
  
  .login_ed>span>span{
    display:inline-block;
    font-weight:800;
    font-size:15px;
    transform:rotate(-90deg);
  }
  .login_ed>span{
